import React from "react";
import { atom, selector, useRecoilState, useRecoilValue } from "recoil";
import _ from "lodash";
import { Article, CopyBlock } from "../../ui";
import styled from "styled-components";

const textState = atom({
  key: "textState",
  default: "",
});

/**
 * selector  Hook  
 */
const charCountState = selector({
  key: "charCountState",
  get: ({ get }) => {
    const text = get(textState);
    return text.length;
  },
});

function TextingPage() {
  const [text, setText] = useRecoilState(textState);
  const count = useRecoilValue(charCountState);

  const changeText = (e: React.ChangeEvent<HTMLInputElement>) => {
    debouncedSearch(e.target.value);
  };

  const debouncedSearch = React.useMemo(
    () =>
      _.debounce((text) => {
        setText(text);
      }, 1),
    [text]
  );
  //@ts-ignore

  return (
    <div>
      <StyledChunk>
        <input type="text" value={text} onChange={changeText} />
        <br />
        Echo: {text}
        <br />
        Count: {count}
      </StyledChunk>
      <Article>
        <CopyBlock
          text={`const [text, setText] = useRecoilState(textState);`}
        />
        <p>
           atom    {" "}
          <code>useRecoilState()</code> .
        </p>

        <CopyBlock
          text={`const textState = atom({
  key: 'textState', // unique ID (with respect to other atoms/selectors)
  default: '', // default value (aka initial value)
});`}
        />
        <p>
          <code>key</code>   . <code>default</code>
           .
        </p>
      </Article>
    </div>
  );
}

export default TextingPage;
const StyledChunk = styled.div`
  padding: 24px 0;
`;
